import React from 'react';
import './inputItem.scss';

function InputItem(props) {
  const { customStyle, disabled, onChange, customInputStyle, value } = props;
  return (
    <div className={`input-item-container ${customStyle}`}>
      {props.icon}
      <input
        className={`input ${customInputStyle}`}
        placeholder={props.text}
        readOnly={disabled}
        onChange={(e) => {
          onChange(e.target.value);
        }}
        value={value}
      />
      {/* {isValid ? null : <p>aaaaaa</p>} */}
    </div>
  );
}

export default InputItem;
